<template>
  <section class="datapicker">
    <DatePickerInput
      :selectedDateFormat="selectedDateFormat ? selectedDateFormat : customDateFormat"
      @update:toggleCalendar="toggleCalendar"
    >
      <template #popup>
        <div class="datapicker__close-layer" @click="toggleCalendar" v-show="isShowCalendar"></div>
        <CalendarWidget :date="customDate" @onSelect="onSelect($event)" v-show="isShowCalendar" />
      </template>
    </DatePickerInput>
  </section>
</template>

<script>
import DatePickerInput from '@/components/DatePickerInput';
import CalendarWidget from '@/components/CalendarWidget';
export default {
  name: 'TheDatePicker',
  data() {
    return {
      // customDate: '2019-12-20', // 可以放字串
      customDate: { year: 2019, month: 11, date: 20 }, // 可以放物件
      selectedDate: {},
      isShowCalendar: false,
    };
  },
  computed: {
    customDateFormat() {
      return Object.keys(this.customDate).length
        ? `${this.customDate.year}-${this.customDate.month + 1}-${
          this.customDate.date
        }`
        : '';
    },
    selectedDateFormat() {
      return Object.keys(this.selectedDate).length
        ? `${this.selectedDate.year}-${this.selectedDate.month + 1}-${
          this.selectedDate.date
        }`
        : '';
    },
  },
  created() {},
  mounted() {},
  methods: {
    toggleCalendar() {
      this.isShowCalendar = !this.isShowCalendar;
    },
    onSelect({ selectedDate, isShowCalendar }) {
      this.selectedDate = selectedDate;
      this.isShowCalendar = isShowCalendar;
    },
  },
  components: {
    DatePickerInput,
    CalendarWidget,
  },
};
</script>

<style lang="scss" scoped>
  .datapicker {
    position: relative;

    &__close-layer {
      width: 100%;
      height: 100vh;
      background-color: transparent;
      position: fixed;
      top: 0;
      left: 0;
    }
  }
</style>
